.counter {
    border: 4px solid cyan;
    width: 550px;
    height: 400px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}


.wrapper {
    border: 4px solid cyan;
    border-radius: 10px;
    margin-bottom: 20px;
    width: 500px;
    height: 92px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
